import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image/withIEPolyfill"

import styles from "./hippo-meow-v1.module.scss"
import EmptyLine from "../../components/empty-line"
import hippoMeowV1Image from "../../images/hippoMeowV1.png"
import hippoMeowV1FontsImage from "../../images/hippoMeowV1Fonts.png"

export default () => {
  const { mockupHippoMeowV1 } = useStaticQuery(graphql`
    query {
      mockupHippoMeowV1: file(relativePath: { eq: "mockupHippoMeowV1.png" }) {
        childImageSharp {
          fixed(height: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  return (
    <React.Fragment>
      <div className={styles.pageHeadingSection}>
        <Img
          fixed={mockupHippoMeowV1.childImageSharp.fixed}
          alt=""
          className={styles.pageHeadingImage}
        />
      </div>
      <div className={styles.pageMainContentSection}>
        <div className={styles.pageMainContent}>
          <EmptyLine />
          <div>
            <div className={styles.projectTitle}>Hippomeow</div>
            <div className={styles.projectSubTitle}>
              Cake &amp; Cafe Shop Website
            </div>
          </div>
          <EmptyLine variant="Paragraph" />
          <div className={styles.projectText}>
            <div>
              I work on an idea for a family cake and cafe shop website.
            </div>
            <EmptyLine variant="Paragraph" />
            <div>
              My aim is to bring a fresh and friendly atmosphere for the
              homepage. Because this is a family shop, I want to tell the users
              a love story while they are scrolling up and down the page.
            </div>
            <EmptyLine variant="Paragraph" />
            <div>
              Users can easily and quickly scan through brief notes about the
              owners, place, and especially the menu of this shop. All necessary
              information is available on the homepage.
            </div>
            <EmptyLine variant="Paragraph" />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Main color:</div>
              <div>
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.green}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.red}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.black}`}
                />
                <span
                  className={`${styles.projectDetailsColorBox} ${styles.white}`}
                />
              </div>
            </div>
            <EmptyLine variant="Paragraph" />
            <div className={styles.projectDetailsRow}>
              <div className={styles.projectDetailsLabel}>Fonts:</div>
              <img src={hippoMeowV1FontsImage} alt="" />
            </div>
            <EmptyLine variant="Paragraph" />
            <div>Full page:</div>
            <EmptyLine variant="Paragraph" />
          </div>
        </div>
      </div>
      <div className={styles.projectImageContainer}>
        <img src={hippoMeowV1Image} className={styles.projectImage} alt="" />
      </div>
      <EmptyLine />
    </React.Fragment>
  )
}
